import uniIcon from '@/components/uni-icon/uni-icon.vue';
export default {
	data() {
		return {
			loading: false,
			stop: false,
			miao: 60,
			loginname: '',
			telephone: '',
			code: '',
			password: '',
			re_password: '',
			title: '注册'
		};
	},
	components: { uniIcon },
	methods: {
		login_action(){
			uni.reLaunch({
				url:'/pages/my/login/login'
			})
		},

		handleReg() {
			const { loginname, password ,telephone,re_password} = this;
			if (loginname.length == 0) {
				this.$api.msg('登录账号不能为空！');
				return;
			}
			if (password.length == 0) {
				this.$api.msg('登录密码不能为空！');
				return;
			}
			if (re_password.length == 0) {
				this.$api.msg('重复密码不能为空！');
				return;
			}
			if (telephone.length != 11) {
				this.$api.msg('手机号码不正确！');
				return;
			}
			const sendData = {
				loginname,
				password,
				telephone
			};
			this.loading = true;
			// uni.showLoading({
			//             title: '正在请求中'
			//         });
			let url = 'http://120.210.132.94:5599/api/BseUser/Regist';
			uni.request({
				url: url,
				data: sendData,
				header: {
					'Content-Type': 'application/json'
				},
				method: 'POST',
				success: res => {
					if (res.statusCode == 200 && res.data.returnCode === '0000') {
						console.log(res.data.data);
						this.login_action();
						// let userinfo = {
						// 	token: res.data.data.token,
						// 	exp: res.data.data.exp
						// };
						// uni.setStorage({
						// 	key: 'userinfo',
						// 	data: userinfo,
						// 	success: function() {
						// 		uni.switchTab({
						// 			url: '/pages/index/index'
						// 		});
						// 	}
						// });
						
					} else {
						this.$api.msg('登录失败');
					}
				},
				fail: () => {
					this.$api.msg('请求失败fail');
				},
				complete: () => {
					this.loading = false;
					//uni.hideLoading();
				}
			});
		},
		send() {
			const mobile = this.mobile;
			this.settime(60);
			if (mobile.length != 11) {
				this.$api.msg('手机号错误');
				return;
			}
		},
		settime(smiao) {
			const that = this;
			const miao = that.miao;
			if (miao == 0) {
				that.miao = 0;
				this.stop = false;
			} else {
				setTimeout(() => {
					smiao--;
					this.miao = smiao;
					that.settime(smiao);
				}, 1000);
			}
		}
	}
};
